___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --primary-color: linear-gradient(to right, #4b0082, #9b30ff);
    --button-color: #104e81;
}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    font-size: 1.3em;
    line-height: 1.5em;
}

.bgprimary {
    background: linear-gradient(to right, #4b0082, #9b30ff);
    width: 100%;
    height: 100vh;
}

.container {
    width: 600px;
    background: #fff;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 5px 5px 50px 5px black; 
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: fadeInUp 0.8s ease-out both;
}

h1 {
    text-align: center;
}

button {
    height: 40px;
    border: none;
    background: var(--primary-color);
    font-weight: 700;
    cursor: pointer;
    border-radius: 20px;
    font-size: 15px;
    text-transform: uppercase;
    color: white;
    transition: 0.3 ease;
    width: 100%;
}

input {
    font: inherit;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 12px;
    box-shadow: 2px 2px 2px 2px rgb(0, 0, 0, 0.088);
    margin-bottom: 20px;
    width: 100%;
    cursor: text;
    outline: none;
}

input:focus {
    border-color: #104e81;
    box-shadow: 0 0 0 3px rgba(155, 48, 255, 0.3);
}

button:hover {
    background: var(--button-color);
    transform: scale(1.05);
    box-shadow: 0 4px 15px rgba(178, 102, 255, 0.5);
}

button:active {
    background: var(--button-color);
}

.resultado {
    text-align: center;
    background: rgb(153, 240, 153);
    border: 1px solid #ccc;
    border-radius: 10px;
    max-width: auto;
    width: 250px;
    margin: 30px auto;
    font-weight: bold;
    font-size: 25px;
}

.CPFvalido {
    text-align: center;
    background: rgb(153, 240, 153);
    border: 1px solid #ccc;
    border-radius: 10px;
    max-width: auto;
    width: 200px;
    margin: 30px auto 0 auto;
    font-weight: bold;
    font-size: 25px;
}

.CPFinvalido {
    text-align: center;
    background: rgb(240, 153, 153);
    border: 1px solid #ccc;
    border-radius: 10px;
    width: 200px;
    margin: 30px auto 0 auto;
    font-weight: bold;
    font-size: 25px;
}

@keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translateY(-50%);
    }

    100% {
        opacity: 1;
        transform: translate(-50%, -50%);
    }
}`, "",{"version":3,"sources":["webpack://./src/assets/css/styles.css"],"names":[],"mappings":"AAEA;IACI,4DAA4D;IAC5D,uBAAuB;AAC3B;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,SAAS;IACT,UAAU;IACV,kCAAkC;IAClC,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,uDAAuD;IACvD,WAAW;IACX,aAAa;AACjB;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,aAAa;IACb,mBAAmB;IACnB,kCAAkC;IAClC,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,sCAAsC;AAC1C;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,gCAAgC;IAChC,gBAAgB;IAChB,eAAe;IACf,mBAAmB;IACnB,eAAe;IACf,yBAAyB;IACzB,YAAY;IACZ,oBAAoB;IACpB,WAAW;AACf;;AAEA;IACI,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,+CAA+C;IAC/C,mBAAmB;IACnB,WAAW;IACX,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,qBAAqB;IACrB,6CAA6C;AACjD;;AAEA;IACI,+BAA+B;IAC/B,sBAAsB;IACtB,+CAA+C;AACnD;;AAEA;IACI,+BAA+B;AACnC;;AAEA;IACI,kBAAkB;IAClB,8BAA8B;IAC9B,sBAAsB;IACtB,mBAAmB;IACnB,eAAe;IACf,YAAY;IACZ,iBAAiB;IACjB,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,8BAA8B;IAC9B,sBAAsB;IACtB,mBAAmB;IACnB,eAAe;IACf,YAAY;IACZ,wBAAwB;IACxB,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,8BAA8B;IAC9B,sBAAsB;IACtB,mBAAmB;IACnB,YAAY;IACZ,wBAAwB;IACxB,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI;QACI,UAAU;QACV,2BAA2B;IAC/B;;IAEA;QACI,UAAU;QACV,gCAAgC;IACpC;AACJ","sourcesContent":["@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');\r\n\r\n:root {\r\n    --primary-color: linear-gradient(to right, #4b0082, #9b30ff);\r\n    --button-color: #104e81;\r\n}\r\n\r\n* {\r\n    box-sizing: border-box;\r\n}\r\n\r\nbody {\r\n    margin: 0;\r\n    padding: 0;\r\n    font-family: 'Poppins', sans-serif;\r\n    font-size: 1.3em;\r\n    line-height: 1.5em;\r\n}\r\n\r\n.bgprimary {\r\n    background: linear-gradient(to right, #4b0082, #9b30ff);\r\n    width: 100%;\r\n    height: 100vh;\r\n}\r\n\r\n.container {\r\n    width: 600px;\r\n    background: #fff;\r\n    padding: 20px;\r\n    border-radius: 20px;\r\n    box-shadow: 5px 5px 50px 5px black; \r\n    position: absolute;\r\n    top: 50%;\r\